import { LitElement, html } from 'lit-element'
import { connect } from 'pwa-helpers'
import { store, actions } from "../redux"
import { PAGES } from "../pages"
import "./arScene"
import "./introSteps"

class App extends connect(store)(LitElement) {
  static get properties() {
    return {
      currentView: { type: String }
    }
  }

  stateChanged(state) {
    this.currentView = state.currentView
  }

  constructor() {
    super()

    const queryParams = new URL(window.location.href).searchParams
    store.dispatch(actions.updateCoordinates(
      queryParams.get("lat"),
      queryParams.get("long")
    ))
  }
  
  render() {
    return html`${this.defineView()}`
  }

  defineView() {
    if (this.currentView == PAGES.ARRIVED) {
      return html`<ar-scene />`
    } else {
      return html`<intro-steps />`
    }
  }
}

customElements.define('app-start', App)
